<template lang="pug">
  div.flag
    JumboLocal(:sided="true")
      .container(slot="header")
        div.jumbo-local--left
          h1 Лицензии и сертификаты
          p В России деятельность таких компаний, как наша, подлежит лицензированию. Обязательным является наличие лицензии на деятельность по технической защите конфиденциальной информации для выполнения работ в части персональных данных и стандарта Банка России.
        div.jumbo-local--right
    .licenses
      .container
        div
          .head__line.head__line--green
          h2
            span Техническая защита конфиденциальной информации
          p Лицензия на деятельность по технической защите конфиденциальной информации № 1904 от 10 декабря 2012 года
          img(src="static/about/licenses/tzki.png")
        div
          .head__line.head__line--blue
          h2
            span Разработка и проектирование средств защиты информации
          p Лицензия на деятельность по разработке и проектированию средств защиты конфиденциальной информации № 1074 от 10 декабря 2012 года
          img(src="static/about/licenses/rip.png")
        div
          .head__line.head__line--orange
          h2 
            span Система менеджмента качества выполнения работ
          p Сертификат соответствия № РОСС RU.3745.04УЛЛ0 / СМК.162-12 от 22 мая 2012 года о соответствии требованиям ГОСТ Р ИСО 9001-2008 (ISO 9001:2008)
          img(src="static/about/licenses/9001.png")
</template>

<script>
import JumboLocal from '../../JumboLocal'

export default {
  name: 'company-licenses',
  components: {
    JumboLocal
  }
}
</script>

<style lang="less">
.flag {
  background: url('/static/about/licenses/bg2.jpg') no-repeat;
  background-size: 100% auto;
  background-color: #fff;
  .jumbo-local {
    background-color: transparent;
  }
}
.licenses {
  background-color: transparent;
  color: #000;
  padding-bottom: 100px;
  .container {
    display: flex;
    flex-direction: row;
    div {
      padding: 0 15px;
      flex: 1 1 33%
    }
    h2 {
      font-weight: 600;
      font-size: 18px;
      margin: 0;
      height: 60px;
      overflow: hidden;
    }
    p {
      margin: 10px 20px 5px 0;
      display: block;
      height: 100px;
      overflow: hidden;
    }
    img {
      border: 1px solid #cccbcb;
      border-radius: 5px;
      padding: 10px;
    }
  }
}

.jumbo--hint {
  font-size: 12px;
  margin-top: 20px;
  padding-top: 15px;
  width: 60%;
  margin-left: 20%;
  img {
    border-bottom: 1px solid #000;
    margin-bottom: 20px;
  }
  p {
    display: block;
  }
}
</style>



