import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/router/Hello'

import Company from '@/components/router/Company'
import About from '@/components/router/company/About'
import Licenses from '@/components/router/company/Licenses'
import Clients from '@/components/router/company/Clients'
import Partners from '@/components/router/company/Partners'

import Pdn from '@/components/router/services/Pdn'
import Cbr from '@/components/router/services/Cbr'
import Expert from '@/components/router/services/Expert'
import Support from '@/components/router/services/Support'
import Auto from '@/components/router/services/Auto'
import Sale from '@/components/router/services/Sale'

import AegisPDM from '@/components/router/products/AegisPDM'
import AegisAudit from '@/components/router/products/AegisAudit'
import AegisU from '@/components/router/products/AegisU'
import AegisAM from '@/components/router/products/AegisAM'
import AegisBoson from '@/components/router/products/AegisBoson'
import AegisBCM from '@/components/router/products/AegisBCM'

Vue.use(Router)

let r = new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/company',
      component: Company,
      children: [
        {
          path: '/',
          name: 'About',
          component: About
        },
        {
          path: '/licenses',
          name: 'Licenses',
          component: Licenses
        },
        {
          path: '/clients',
          name: 'Clients',
          component: Clients
        },
        {
          path: '/partners',
          name: 'Partners',
          component: Partners
        }
      ]
    },
    {
      path: '/pdn',
      name: 'Pdn',
      component: Pdn
    },
    {
      path: '/cbr',
      name: 'Cbr',
      component: Cbr
    },
    {
      path: '/expert',
      name: 'Expert',
      component: Expert
    },
    {
      path: '/support',
      name: 'Support',
      component: Support
    },
    {
      path: '/auto',
      name: 'Auto',
      component: Auto
    },
    {
      path: '/sale',
      name: 'Sale',
      component: Sale
    },
    {
      path: '/aegis-pdm',
      name: 'AegisPDM',
      component: AegisPDM
    },
    {
      path: '/aegis-audit',
      name: 'AegisAudit',
      component: AegisAudit
    },
    {
      path: '/aegis-u',
      name: 'AegisU',
      component: AegisU
    },
    {
      path: '/aegis-am',
      name: 'AegisAM',
      component: AegisAM
    },
    {
      path: '/aegis-boson',
      name: 'AegisBoson',
      component: AegisBoson
    },
    {
      path: '/aegis-bcm',
      name: 'AegisBCM',
      component: AegisBCM
    }
  ]
})

r.beforeEach(function (to, from, next) {
  window.scrollTo(0, 0)
  next()
})

export default r



