<template lang="pug">
  .jumbo-local(v-bind:class="{ 'jumbo-local--inverse': inverse, 'jumbo-local--sided': sided }")
    slot(name="header")
</template>

<script>
export default {
  name: 'app',
  props: [
    'inverse',
    'sided'
  ]
}
</script>

<style lang="less">
.jumbo-local {
  background-color: #fff;
  min-height: 650px;
  text-align: center;
  font-size: 18px;

  .container {
    padding: 80px 0;
  }
  
  h1 {
    font-size: 60px;
    line-height: 60px;
    font-weight: 700;
    &--condensed {
      width: 600px;
    }
  }

  p {
    padding: 20px 15%;
  }

  &--inverse {
    background-color: #000;
    color: #fff;
  }
  
  &--sided > .container {
    display: flex;
    flex-direction: row;
    text-align: left;
    h1 {
      margin-bottom: 30px;
      margin-left: 10px;
    }
    p {
      padding: 0 !important;
      margin-left: 10px;
    }
  }

  &--left {
    flex: 0 0 500px;
  }

  &--right {
    flex-grow: 1;
  }
}
</style>



