<template lang="pug">
  .submenu(v-sticky="{ zIndex: 1000, stickyTop: 0 }")
    .container
      .submenu__wrapper
        .submenu__logo
          slot(name="header")
        .submenu__menu
          slot(name="right")
</template>

<script>
import VueSticky from 'vue-sticky'

export default {
  name: 'aes-submenu',
  directives: {
    'sticky': VueSticky
  }
}
</script>

<style lang="less">
@submenu-height: 60px;

.submenu {
  border-bottom: 1px solid #cccbcb;
  height: @submenu-height;
  background-color: #fff;
}
.submenu__wrapper {
  display: flex;
  flex-direction: row;
  height: @submenu-height;
}
.submenu__logo {
  min-width: 0;
  align-self: center;
  font-family: 'Open Sans', sans-serif;
  color: #000;
  h1 {
    font-size: 22px;
    font-weight: 700;
    margin: 0;
  }
}
.submenu__menu {
  margin-left: auto;
  align-self: center;
  height: 100%;
  font-size: 13px;
  ul {
    display: flex;
    flex-direction: row;
    margin: 0;
    padding: 0;
    height: 100%;
    list-style-type: none;
    li {
      margin-left: 15px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 0 10px;
      cursor: pointer;
      border-bottom: 4px solid transparent;
      border-top: 4px solid transparent;
      &:hover {
        color: #18a3fa;
        cursor: pointer;
      }
      &.router-link-active {
        border-bottom-color: #000;
      }
    }
  }
}
</style>



